/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
  height: 100%;
  font-family: "Roboto Condensed", sans-serif;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-size: 1.8rem;
  margin: 0;
  padding: 0;
  padding: 0 !important;
  display: flex;
  flex-direction: column;
}

a {
  text-decoration: none;
}

.contenedor {
  width: 90%;
  max-width: 1200px;
  padding: 0 auto;
}
@media (min-width: 768px) {
  .contenedor {
    width: 95%;
  }
}

.contenedor-sm {
  max-width: 70rem;
  margin: 0 auto;
  padding: 0 3rem;
}

.principal {
  flex: 1;
}

.desactivado {
  opacity: 0.3;
}

h1, h2, h3 {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
}

h1 {
  font-size: 4rem;
}

h2 {
  font-size: 3.6rem;
}

h3 {
  font-size: 3rem;
}

.text-center {
  text-align: center;
}

[class$=__heading] {
  font-family: "Roboto Condensed", sans-serif;
  font-weight: 900;
  margin: 2rem 0;
  margin: 6rem 0 3rem 0;
  text-align: center;
}

[class$=__descripcion] {
  text-align: center;
  font-size: 1.8rem;
  font-weight: 300;
  color: #1E293B;
  margin-bottom: 5rem;
}

.header {
  display: flex;
  justify-content: space-around;
  align-items: center;
  background-color: #000000;
  padding: 2rem 0;
  border-bottom: 1px solid #560257;
  position: fixed;
  width: 100%;
  height: 10rem;
}
@media (min-width: 1024px) {
  .header {
    position: inherit;
  }
}
.header .logo {
  width: 10rem;
}
.header .menu {
  text-align: center;
  display: flex;
  gap: 3.5rem;
  flex-direction: column;
  position: fixed;
  right: -100%;
  top: 9rem;
  background-color: #560257;
  padding: 2rem;
  border: #A700A8 1px solid;
  border-radius: 1rem;
  transition: all 0.5s;
  z-index: 10;
}
@media (min-width: 1024px) {
  .header .menu {
    gap: 6rem;
    display: flex;
    flex-direction: row;
    position: inherit;
    background-color: transparent;
    border: none;
  }
}
.header .menu a {
  text-decoration: none;
  color: #ff00ff;
  font-size: 1.9rem;
}
@media (min-width: 1024px) {
  .header .menu a {
    font-size: 2.2rem;
  }
}
.header .menu.activo {
  right: 2.6rem;
}
.header .abrir_menu {
  color: #ff00ff;
  font-size: 3rem;
  cursor: pointer;
}
@media (min-width: 1024px) {
  .header .abrir_menu {
    display: none;
  }
}

.main-contenedor {
  margin-top: 10rem;
}
@media (min-width: 1024px) {
  .main-contenedor {
    margin-top: 0;
  }
}
.main-contenedor .caja_bienvenida {
  background-image: linear-gradient(180deg, #121212, #59025a);
  padding: 2rem 4rem;
}
.main-contenedor .caja_bienvenida .bienvenida {
  display: flex;
  justify-content: center;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .bienvenida {
    margin-top: 5rem;
  }
}
.main-contenedor .caja_bienvenida .bienvenida h1 {
  font-size: 3rem;
  text-align: center;
  color: rgb(226, 226, 226);
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .bienvenida h1 {
    font-size: 5rem;
  }
}
.main-contenedor .caja_bienvenida .jose_info {
  justify-content: space-around;
  text-align: center;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .jose_info {
    justify-content: center;
  }
}
.main-contenedor .caja_bienvenida .jose_info p {
  text-align: center;
  margin-top: 3rem;
  font-size: 1.5rem;
  margin-bottom: 0;
  color: rgb(226, 226, 226);
  padding-bottom: 2rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .jose_info .contenedor_img {
    display: flex;
    align-items: center;
  }
}
.main-contenedor .caja_bienvenida .jose_info .contenedor_img .jose {
  width: 30vw;
  min-width: 20rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .jose_info .contenedor_img .jose {
    width: 20vw;
    min-width: 30rem;
    max-width: 40rem;
  }
}
.main-contenedor .caja_bienvenida .jose_info .redes_sociales {
  display: flex;
  justify-content: center;
  gap: 2.5rem;
}
.main-contenedor .caja_bienvenida .jose_info .redes_sociales a {
  font-size: 3rem;
  color: rgb(226, 226, 226);
  transition: all 0.2s ease;
}
.main-contenedor .caja_bienvenida .jose_info .redes_sociales a:hover {
  transform: scale(1.2);
}
@media (min-width: 1024px) {
  .main-contenedor .caja_bienvenida .jose_info {
    display: flex;
    padding: 5rem 4rem 8rem 4rem;
  }
  .main-contenedor .caja_bienvenida .jose_info .info_redes {
    margin: auto 5rem;
    width: 70vw;
    max-width: 70rem;
  }
  .main-contenedor .caja_bienvenida .jose_info .info_redes p {
    font-size: 2rem;
  }
}
.main-contenedor .caja_servicios {
  color: rgb(226, 226, 226);
  background-color: #320132;
  padding: 5rem 4rem 8rem 4rem;
}
.main-contenedor .caja_servicios h2 {
  text-align: center;
  margin: 2rem auto 4rem auto;
  font-size: 3.5rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_servicios h2 {
    font-size: 4rem;
  }
}
.main-contenedor .caja_servicios .servicios {
  text-align: center;
  display: grid;
  place-items: center;
  gap: 10rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .main-contenedor .caja_servicios .servicios {
    grid-template-columns: repeat(3, 1fr);
    margin: 0 auto;
    max-width: 130rem;
    gap: 0;
  }
}
.main-contenedor .caja_servicios .servicios .servicio {
  width: 70%;
}
.main-contenedor .caja_servicios .servicios .servicio .tecnologias {
  display: flex;
  justify-content: center;
  gap: 1rem;
}
.main-contenedor .caja_servicios .servicios .servicio .tecnologias img {
  width: 10vw;
  min-width: 4rem;
  max-width: 6rem;
}
.main-contenedor .caja_servicios .servicios h3 {
  font-size: 2.5rem;
}
.main-contenedor .caja_servicios .servicios p {
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_servicios .servicios p {
    font-size: 2rem;
  }
}
.main-contenedor .caja_servicios .servicios i {
  font-size: 5rem;
}
.main-contenedor .caja_proyectos {
  color: rgb(226, 226, 226);
  background-color: #191919;
  padding: 4rem;
  border-top: solid 2px #a3007a;
}
.main-contenedor .caja_proyectos h2 {
  text-align: center;
  margin: 2rem auto 4rem auto;
  font-size: 3.5rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos h2 {
    font-size: 4rem;
  }
}
.main-contenedor .caja_proyectos .proyectos {
  text-align: center;
  display: grid;
  place-items: center;
  gap: 5rem;
  grid-template-columns: repeat(1, 1fr);
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos .proyectos {
    margin: 0 auto;
    max-width: 130rem;
    gap: 0;
  }
}
.main-contenedor .caja_proyectos .proyectos h3 {
  font-size: 2.5rem;
}
.main-contenedor .caja_proyectos .proyectos p {
  font-size: 1.5rem;
}
.main-contenedor .caja_proyectos .proyectos p a {
  color: #df00a7;
  font-size: 1.6rem;
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos .proyectos p a {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos .proyectos p {
    font-size: 2rem;
  }
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos .proyectos {
    grid-template-columns: repeat(3, 1fr);
  }
}
.main-contenedor .caja_proyectos .proyectos .proyecto img {
  width: 13rem;
  transition: all 0.3s ease;
}
.main-contenedor .caja_proyectos .proyectos .proyecto img:hover {
  transform: scale(1.1);
}
@media (min-width: 1024px) {
  .main-contenedor .caja_proyectos .proyectos .proyecto {
    width: 70%;
  }
}
.main-contenedor .contactame {
  background-color: #320132;
  color: rgb(226, 226, 226);
  padding: 4rem;
  border-top: solid 2px #a3007a;
  border-bottom: solid 2px #a3007a;
}
@media (min-width: 1024px) {
  .main-contenedor .contactame .caja_contactame {
    max-width: 100rem;
    margin: 0 auto;
  }
}
.main-contenedor .contactame h2 {
  margin: 0 0 3rem 0;
  text-align: center;
  font-size: 3rem;
}
.main-contenedor .contactame p {
  margin: 0 0 3rem 0;
  text-align: center;
  font-size: 1.5rem;
}
@media (min-width: 1024px) {
  .main-contenedor .contactame p {
    width: 80%;
    margin: 3rem auto;
    font-size: 2rem;
  }
}
.main-contenedor .contactame .redes_sociales {
  display: flex;
  justify-content: center;
  gap: 2.5rem;
}
.main-contenedor .contactame .redes_sociales a {
  font-size: 4rem;
  color: rgb(226, 226, 226);
  transition: all 0.2s ease;
}
.main-contenedor .contactame .redes_sociales a:hover {
  transform: scale(1.2);
}
.main-contenedor .contactame .redes_sociales .flecha {
  display: flex;
  align-items: center;
  font-size: 4rem;
  color: rgb(251, 255, 0);
}
.main-contenedor .contactame .redes_sociales .izquierda {
  animation: mover-izquierda 0.4s infinite alternate; /* Animación de movimiento hacia la izquierda */
}
.main-contenedor .contactame .redes_sociales .derecha {
  animation: mover-derecha 0.4s infinite alternate; /* Animación de movimiento hacia la derecha */
}
@keyframes mover-izquierda {
  from {
    transform: translateX(0); /* Posición inicial */
  }
  to {
    transform: translateX(-24px); /* Posición final */
  }
}
@keyframes mover-derecha {
  from {
    transform: translateX(0); /* Posición inicial */
  }
  to {
    transform: translateX(24px); /* Posición final */
  }
}

.footer {
